.catalog {
  padding-top: 38px;
}

.catalog a {
  text-decoration: none;
}

.catalog__present {
  display: grid;
  grid-template-columns: 1fr max-content;
  column-gap: 2vw;

  /*
  padding-left: 132px;
  padding-right: 132px;
  */

  padding-left: 6vw;
  padding-right: 6vw;

  @media (max-width: 1280px) {
    padding-left: 0;
    padding-right: 0;
  }

  @media (max-width: 767px) {
    grid-template-columns: 1fr;
    text-align: center;
  }

  outline: 1px solid red;

}

.catalog__present > div {
  max-width: 1100px;

  @media (max-width: 767px) {
    max-width: 100%;
  }
}

.catalog__present p {
  display: block;
  padding-right: 120px;
  margin-top: 32px;

  @media (max-width: 767px) {
    padding: 0;
  }
}

.catalog__cup {
  width: clamp(157px, 12vw, 235px);
  height: clamp(157px, 12vw, 235px);
  margin-left: auto;

  @media (max-width: 767px) {
    width: 235px;
    height: 235px;
    margin: 0 auto;
  }
}

.catalog__list {
  margin-top: 134px;
}
