.users-gallery {
  padding-top: 115px;
}

.users-gallery__present {
  display: grid;
  grid-template-columns: max-content 1fr;
  column-gap: 2vw;
  align-items: center;
  padding-left: 6vw;
  padding-right: 6vw;
  padding-bottom: 47px;
}

.users-gallery__present.container {
  padding-left: 10.4vw;
  padding-right: 10.4vw;

  @media (max-width: 1280px) {
    padding-left: 3.6vw;
    padding-right: 3.6vw;
  }
}

.users-gallery h3,
.users-gallery p {
  margin: 0;
}

.users-gallery p {
  max-width: 560px;
}
